import type { OverlayPlugin } from '../index';

export const NotificationBannerRemovalPlugin: OverlayPlugin = {
	id: 'notification-banner-removal',
	priority: 90,
	init: async ({ logger }) => {
		logger.debug('NotificationBannerRemovalPlugin init');
	},
	start: async ({ logger }) => {
		// Remove notification banner bar
		const notificationBanner = document.querySelector('[data-test="notificationBannerBar"]');
		if (notificationBanner) {
			notificationBanner.remove();
			logger.info('Notification banner bar removed from DOM');
		} else {
			logger.debug('Notification banner bar not found');
		}

		logger.info('NotificationBannerRemovalPlugin started');
	},
	stop: async ({ logger }) => {
		logger.info('NotificationBannerRemovalPlugin stopped');
	},
};
