import type { OverlayPlugin } from '../index';
import { TimeDisplayManager } from '../modules/time-display.ts';

export const TimeDisplayPlugin: OverlayPlugin = {
	id: 'time-display',
	priority: 70,
	init: async ({ logger }) => {
		logger.debug('TimeDisplayPlugin init');
	},
	start: async ({ logger }) => {
		// Initialize time display manager
		const timeDisplayManager = new TimeDisplayManager({
			logger: logger
		});

		// Store manager for cleanup
		(window as any).__masarTimeDisplayManager = timeDisplayManager;

		// Add time display beside connection status button
		timeDisplayManager.addTimeDisplay();

		logger.info('TimeDisplayPlugin started');
	},
	stop: async ({ logger }) => {
		// Remove time display
		if ((window as any).__masarTimeDisplayManager) {
			(window as any).__masarTimeDisplayManager.removeTimeDisplay();
			delete (window as any).__masarTimeDisplayManager;
		}
		logger.info('TimeDisplayPlugin stopped');
	},
};
