import type { OverlayContext, OverlayPlugin } from '../index';

export class PluginRegistry {
	private plugins: OverlayPlugin[] = [];

	register(plugin: OverlayPlugin) {
		if (!plugin || !plugin.id) {
			throw new Error('Plugin must have an id');
		}
		const exists = this.plugins.some((p) => p.id === plugin.id);
		if (exists) {
			throw new Error(`Plugin with id ${plugin.id} already registered`);
		}
		this.plugins.push(plugin);
		this.plugins.sort((a, b) => (b.priority ?? 0) - (a.priority ?? 0));
	}

	async initAll(ctx: OverlayContext) {
		for (const p of this.plugins) {
			if (p.init) await p.init(ctx);
		}
	}

	async startAll(ctx: OverlayContext) {
		for (const p of this.plugins) {
			if (p.start) await p.start(ctx);
		}
	}

	async stopAll(ctx: OverlayContext) {
		for (const p of this.plugins) {
			if (p.stop) await p.stop(ctx);
		}
	}
}


