/**
 * Video Icon Replacement Plugin
 *
 * Replaces video icons with custom SVG content.
 */

import type { OverlayPlugin } from '../index';
import { VideoIconReplacement } from '../modules/video-icon-replacement.ts';

export const VideoIconReplacementPlugin: OverlayPlugin = {
	id: 'video-icon-replacement',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('VideoIconReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const videoIconReplacement = new VideoIconReplacement({ logger });
		
		// Store for cleanup
		(window as any).__masarVideoIconReplacement = videoIconReplacement;
		
		// Start watching for video icons (includes initial replacement)
		videoIconReplacement.startWatching();
		
		logger.info('VideoIconReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarVideoIconReplacement) {
			const replacement = (window as any).__masarVideoIconReplacement;
			replacement.stopWatching();
			replacement.restoreVideoIcons();
			delete (window as any).__masarVideoIconReplacement;
		}
		
		logger.info('VideoIconReplacementPlugin stopped');
	},
};
