/**
 * Video Icon Replacement Module
 *
 * Replaces video icons with custom SVG content.
 */

import { VIDEO_OFF, VIDEO_ON } from '../constants/index.ts';

export interface VideoIconReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class VideoIconReplacement {
	private logger: VideoIconReplacementOptions['logger'];
	private replacedElements: Map<HTMLElement, string> = new Map();
	private observer: MutationObserver | null = null;
	private intervalId: number | null = null;

	constructor(options: VideoIconReplacementOptions) {
		this.logger = options.logger;
	}

	private replaceSingleVideoIcon(element: HTMLElement): void {
		const hasVideoOffClass = element.classList.contains('icon-bbb-video_off');
		const hasVideoClass = element.classList.contains('icon-bbb-video');

		// If element was already replaced, we need to update it based on current state
		const wasAlreadyReplaced = this.replacedElements.has(element);

		if (hasVideoOffClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with VIDEO_OFF SVG
			element.innerHTML = VIDEO_OFF;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Remove any background styling from parent span when video is off
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.removeProperty('background');
			}
			
			this.logger.debug('Replaced icon-bbb-video_off with VIDEO_OFF and removed background');
		} else if (hasVideoClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with VIDEO_ON SVG
			element.innerHTML = VIDEO_ON;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Add gray background to the parent span when video is on (sharing)
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.setProperty('background', '#6B7280', 'important');
			}
			
			this.logger.debug('Replaced icon-bbb-video with VIDEO_ON and added gray background');
		}
	}

	replaceVideoIcons(): void {
		// Find and replace all existing video icons
		const videoOffElements = document.querySelectorAll('.icon-bbb-video_off');
		const videoElements = document.querySelectorAll('.icon-bbb-video');
		const iElementsWithVideoOff = document.querySelectorAll('i.icon-bbb-video_off');
		const iElementsWithVideo = document.querySelectorAll('i.icon-bbb-video');

		// Combine all elements and process them
		const allElements = [
			...Array.from(videoOffElements),
			...Array.from(videoElements),
			...Array.from(iElementsWithVideoOff),
			...Array.from(iElementsWithVideo)
		] as HTMLElement[];

		allElements.forEach(element => {
			this.replaceSingleVideoIcon(element);
		});

		const totalReplaced = allElements.length;
		if (totalReplaced > 0) {
			this.logger.info(`Replaced ${totalReplaced} video icons total and removed their classes`);
		} else {
			this.logger.warn('No video icons found to replace');
		}
	}

	startWatching(): void {
		// Initial replacement
		this.replaceVideoIcons();

		// Wait for DOM to be ready if needed
		const startObserver = () => {
			// Set up MutationObserver to watch for new elements
			this.observer = new MutationObserver((mutations) => {
				let hasNewVideoIcons = false;

				mutations.forEach((mutation) => {
					// Check added nodes
					mutation.addedNodes.forEach((node) => {
						if (node.nodeType === Node.ELEMENT_NODE) {
							const element = node as HTMLElement;
							
							// Check if the added element itself has video icon classes
							if (element.classList.contains('icon-bbb-video_off') || 
								element.classList.contains('icon-bbb-video')) {
								this.replaceSingleVideoIcon(element);
								hasNewVideoIcons = true;
							}

							// Check if the added element contains video icon elements
							const videoOffElements = element.querySelectorAll?.('.icon-bbb-video_off');
							const videoElements = element.querySelectorAll?.('.icon-bbb-video');
							
							if (videoOffElements) {
								videoOffElements.forEach(el => {
									this.replaceSingleVideoIcon(el as HTMLElement);
									hasNewVideoIcons = true;
								});
							}
							
							if (videoElements) {
								videoElements.forEach(el => {
									this.replaceSingleVideoIcon(el as HTMLElement);
									hasNewVideoIcons = true;
								});
							}
						}
					});

					// Check for attribute changes (class changes)
					if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
						const element = mutation.target as HTMLElement;
						// Check if this element has video icon classes OR if it was previously replaced
						if (element.classList.contains('icon-bbb-video_off') || 
							element.classList.contains('icon-bbb-video') ||
							this.replacedElements.has(element)) {
							this.replaceSingleVideoIcon(element);
							hasNewVideoIcons = true;
						}
					}
				});

				if (hasNewVideoIcons) {
					this.logger.debug('Found and replaced new video icons via MutationObserver');
				}
			});

			// Start observing
			this.observer.observe(document.body, {
				childList: true,
				subtree: true,
				attributes: true,
				attributeFilter: ['class']
			});

			this.logger.info('Started watching for new video icons');
		};

		// Start observer immediately or wait for DOM ready
		if (document.body) {
			startObserver();
		} else {
			document.addEventListener('DOMContentLoaded', startObserver);
		}

		// Also set up a periodic check as fallback (every 2 seconds)
		this.intervalId = window.setInterval(() => {
			this.logger.debug('Periodic check for video icons...');
			const currentCount = document.querySelectorAll('.icon-bbb-video_off, .icon-bbb-video').length;
			if (currentCount > 0) {
				this.logger.debug(`Found ${currentCount} video icons during periodic check`);
				this.replaceVideoIcons();
			}
		}, 2000);
	}

	stopWatching(): void {
		if (this.observer) {
			this.observer.disconnect();
			this.observer = null;
			this.logger.info('Stopped watching for video icons');
		}
		
		if (this.intervalId) {
			clearInterval(this.intervalId);
			this.intervalId = null;
			this.logger.info('Stopped periodic video icon checks');
		}
	}

	restoreVideoIcons(): void {
		// Restore all replaced elements
		this.replacedElements.forEach((originalContent, element) => {
			element.innerHTML = originalContent;
		});
		this.replacedElements.clear();
		this.logger.info('Video icons restored');
	}
}
