/**
 * Presentation Icon Replacement Plugin
 *
 * Replaces presentation icons with custom SVG content.
 */

import type { OverlayPlugin } from '../index';
import { PresentationIconReplacement } from '../modules/presentation-icon-replacement.ts';

export const PresentationIconReplacementPlugin: OverlayPlugin = {
	id: 'presentation-icon-replacement',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('PresentationIconReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const presentationIconReplacement = new PresentationIconReplacement({ logger });
		
		// Store for cleanup
		(window as any).__masarPresentationIconReplacement = presentationIconReplacement;
		
		// Start watching for presentation icons (includes initial replacement)
		presentationIconReplacement.startWatching();
		
		logger.info('PresentationIconReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarPresentationIconReplacement) {
			const replacement = (window as any).__masarPresentationIconReplacement;
			replacement.stopWatching();
			replacement.restorePresentationIcons();
			delete (window as any).__masarPresentationIconReplacement;
		}
		
		logger.info('PresentationIconReplacementPlugin stopped');
	},
};
