import type { OverlayPlugin } from '../index';
import { HeaderLogoReplacementManager } from '../modules/header-logo-replacement.ts';

export const HeaderLogoReplacementPlugin: OverlayPlugin = {
	id: 'header-logo-replacement',
	priority: 80,
	init: async ({ logger }) => {
		logger.debug('HeaderLogoReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const manager = new HeaderLogoReplacementManager({ logger });
		
		// Store manager for cleanup
		(window as any).__masarHeaderLogoManager = manager;
		
		// Replace the header button with logo
		manager.replaceHeaderButton();

		logger.info('HeaderLogoReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up manager
		if ((window as any).__masarHeaderLogoManager) {
			const manager = (window as any).__masarHeaderLogoManager;
			manager.restoreHeaderButton();
			delete (window as any).__masarHeaderLogoManager;
		}

		logger.info('HeaderLogoReplacementPlugin stopped');
	},
};
