/**
 * Favicon Management Module
 * 
 * Handles favicon changes and management for the application.
 */

export interface FaviconConfig {
	/** The favicon URL (can be data URL) */
	faviconUrl: string;
	/** Logging function for debug messages */
	logger?: {
		debug: (message: string) => void;
		info: (message: string) => void;
	};
}

export class FaviconManager {
	private config: FaviconConfig;
	private originalFavicon: string | null = null;

	constructor(config: FaviconConfig) {
		this.config = config;
	}

	/**
	 * Apply the configured favicon
	 */
	apply(): void {
		if (typeof document === 'undefined') return;

		// Store original favicon for restoration
		const existingFavicon = document.querySelector('link[rel="icon"]') as HTMLLinkElement || 
			document.querySelector('link[rel="shortcut icon"]') as HTMLLinkElement;
		
		if (existingFavicon) {
			this.originalFavicon = existingFavicon.href;
			existingFavicon.href = this.config.faviconUrl;
		} else {
			const newFavicon = document.createElement('link');
			newFavicon.rel = 'icon';
			newFavicon.href = this.config.faviconUrl;
			newFavicon.type = 'image/svg+xml';
			document.head.appendChild(newFavicon);
		}

		this.config.logger?.info('Favicon applied');
	}

	/**
	 * Restore the original favicon
	 */
	restore(): void {
		if (typeof document === 'undefined') return;

		const favicon = document.querySelector('link[rel="icon"]') as HTMLLinkElement || 
			document.querySelector('link[rel="shortcut icon"]') as HTMLLinkElement;

		if (favicon && this.originalFavicon) {
			favicon.href = this.originalFavicon;
		} else if (favicon) {
			favicon.remove();
		}

		this.config.logger?.info('Favicon restored');
	}
}
