/**
 * Audio Icon Replacement Module
 *
 * Replaces audio icons with custom SVG content.
 */

import { AUDIO_OFF, AUDIO_ON } from '../constants/index.ts';

export interface AudioIconReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class AudioIconReplacement {
	private logger: AudioIconReplacementOptions['logger'];
	private replacedElements: Map<HTMLElement, string> = new Map();
	private observer: MutationObserver | null = null;
	private intervalId: number | null = null;

	constructor(options: AudioIconReplacementOptions) {
		this.logger = options.logger;
	}

	private replaceSingleAudioIcon(element: HTMLElement): void {
		// Skip icons inside audioModalOptions to avoid ruining the audio modal
		if (element.closest('[data-test="audioModalOptions"]')) {
			this.logger.debug('Skipping audio icon inside audioModalOptions');
			return;
		}

		const hasAudioOffClass = element.classList.contains('icon-bbb-no_audio');
		const hasMuteClass = element.classList.contains('icon-bbb-mute');
		const hasUnmuteClass = element.classList.contains('icon-bbb-unmute');

		// If element was already replaced, we need to update it based on current state
		const wasAlreadyReplaced = this.replacedElements.has(element);

		if (hasAudioOffClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with AUDIO_OFF SVG
			element.innerHTML = AUDIO_OFF;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Remove any background styling from parent span when audio is off
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.removeProperty('background');
			}
			
			this.logger.debug('Replaced icon-bbb-no_audio with AUDIO_OFF and removed background');
		} else if (hasMuteClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with AUDIO_OFF SVG (muted state)
			element.innerHTML = AUDIO_OFF;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Remove any background styling from parent span when audio is muted
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.removeProperty('background');
			}
			
			this.logger.debug('Replaced icon-bbb-mute with AUDIO_OFF and removed background');
		} else if (hasUnmuteClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with AUDIO_ON SVG (unmuted state)
			element.innerHTML = AUDIO_ON;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Add red background to the parent span when audio is on (unmuted)
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.setProperty('background', '#EF4444', 'important');
			}
			
			this.logger.debug('Replaced icon-bbb-unmute with AUDIO_ON and added red background');
		}
	}

	replaceAudioIcons(): void {
		// Find and replace all existing audio icons
		const audioOffElements = document.querySelectorAll('.icon-bbb-no_audio');
		const muteElements = document.querySelectorAll('.icon-bbb-mute');
		const unmuteElements = document.querySelectorAll('.icon-bbb-unmute');
		const iElementsWithAudioOff = document.querySelectorAll('i.icon-bbb-no_audio');
		const iElementsWithMute = document.querySelectorAll('i.icon-bbb-mute');
		const iElementsWithUnmute = document.querySelectorAll('i.icon-bbb-unmute');

		// Combine all elements and process them
		const allElements = [
			...Array.from(audioOffElements),
			...Array.from(muteElements),
			...Array.from(unmuteElements),
			...Array.from(iElementsWithAudioOff),
			...Array.from(iElementsWithMute),
			...Array.from(iElementsWithUnmute)
		] as HTMLElement[];

		allElements.forEach(element => {
			this.replaceSingleAudioIcon(element);
		});

		const totalReplaced = allElements.length;
		if (totalReplaced > 0) {
			this.logger.info(`Replaced ${totalReplaced} audio icons total and removed their classes`);
		} else {
			this.logger.warn('No audio icons found to replace');
		}
	}

	startWatching(): void {
		// Initial replacement
		this.replaceAudioIcons();

		// Wait for DOM to be ready if needed
		const startObserver = () => {
			// Set up MutationObserver to watch for new elements
			this.observer = new MutationObserver((mutations) => {
				let hasNewAudioIcons = false;

				mutations.forEach((mutation) => {
					// Check added nodes
					mutation.addedNodes.forEach((node) => {
						if (node.nodeType === Node.ELEMENT_NODE) {
							const element = node as HTMLElement;
							
							// Check if the added element itself has audio icon classes
							if (element.classList.contains('icon-bbb-no_audio') || 
								element.classList.contains('icon-bbb-mute') ||
								element.classList.contains('icon-bbb-unmute')) {
								this.replaceSingleAudioIcon(element);
								hasNewAudioIcons = true;
							}

							// Check if the added element contains audio icon elements
							const audioOffElements = element.querySelectorAll?.('.icon-bbb-no_audio');
							const muteElements = element.querySelectorAll?.('.icon-bbb-mute');
							const unmuteElements = element.querySelectorAll?.('.icon-bbb-unmute');
							
							if (audioOffElements) {
								audioOffElements.forEach(el => {
									this.replaceSingleAudioIcon(el as HTMLElement);
									hasNewAudioIcons = true;
								});
							}
							
							if (muteElements) {
								muteElements.forEach(el => {
									this.replaceSingleAudioIcon(el as HTMLElement);
									hasNewAudioIcons = true;
								});
							}
							
							if (unmuteElements) {
								unmuteElements.forEach(el => {
									this.replaceSingleAudioIcon(el as HTMLElement);
									hasNewAudioIcons = true;
								});
							}
						}
					});

					// Check for attribute changes (class changes)
					if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
						const element = mutation.target as HTMLElement;
						// Check if this element has audio icon classes OR if it was previously replaced
						if (element.classList.contains('icon-bbb-no_audio') || 
							element.classList.contains('icon-bbb-mute') ||
							element.classList.contains('icon-bbb-unmute') ||
							this.replacedElements.has(element)) {
							this.replaceSingleAudioIcon(element);
							hasNewAudioIcons = true;
						}
					}
				});

				if (hasNewAudioIcons) {
					this.logger.debug('Found and replaced new audio icons via MutationObserver');
				}
			});

			// Start observing
			this.observer.observe(document.body, {
				childList: true,
				subtree: true,
				attributes: true,
				attributeFilter: ['class']
			});

			this.logger.info('Started watching for new audio icons');
		};

		// Start observer immediately or wait for DOM ready
		if (document.body) {
			startObserver();
		} else {
			document.addEventListener('DOMContentLoaded', startObserver);
		}

		// Also set up a periodic check as fallback (every 2 seconds)
		this.intervalId = window.setInterval(() => {
			this.logger.debug('Periodic check for audio icons...');
			const currentCount = document.querySelectorAll('.icon-bbb-no_audio, .icon-bbb-mute, .icon-bbb-unmute').length;
			if (currentCount > 0) {
				this.logger.debug(`Found ${currentCount} audio icons during periodic check`);
				this.replaceAudioIcons();
			}
		}, 2000);
	}

	stopWatching(): void {
		if (this.observer) {
			this.observer.disconnect();
			this.observer = null;
			this.logger.info('Stopped watching for audio icons');
		}
		
		if (this.intervalId) {
			clearInterval(this.intervalId);
			this.intervalId = null;
			this.logger.info('Stopped periodic audio icon checks');
		}
	}

	restoreAudioIcons(): void {
		// Restore all replaced elements
		this.replacedElements.forEach((originalContent, element) => {
			element.innerHTML = originalContent;
		});
		this.replacedElements.clear();
		this.logger.info('Audio icons restored');
	}
}
