export interface TimeDisplayOptions {
	logger: any;
}

export class TimeDisplayManager {
	private options: TimeDisplayOptions;
	private timeDisplay: HTMLElement | null = null;
	private ringContainer: HTMLElement | null = null;
	private timeInterval: number | null = null;
	private styleElement: HTMLStyleElement | null = null;

	constructor(options: TimeDisplayOptions) {
		this.options = options;
	}

	addTimeDisplay(): void {
		const { logger } = this.options;

		// Find the connection status button
		const connectionButton = document.querySelector('[data-test="connectionStatusButton"]');
		if (!connectionButton) {
			logger.debug('Connection status button not found');
			return;
		}

		// Make the parent container flex
		const parentContainer = connectionButton.parentElement;
		if (parentContainer) {
			parentContainer.style.display = 'flex';
			parentContainer.style.alignItems = 'center';
			parentContainer.style.gap = '10px';
		}

		// Create time display container
		const timeDisplay = document.createElement('div');
		timeDisplay.style.display = 'flex';
		timeDisplay.style.alignItems = 'center';
		timeDisplay.style.gap = '10px';
		timeDisplay.style.backgroundColor = '#0A2540';
		timeDisplay.style.borderRadius = '20px';
		timeDisplay.style.padding = '8px 16px';
		timeDisplay.style.fontSize = '14px';
		timeDisplay.style.fontWeight = '500';
		timeDisplay.style.color = 'white';
		timeDisplay.style.fontFamily = 'monospace';

		// Create SVG clock icon
		const clockIcon = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
		clockIcon.setAttribute('width', '14');
		clockIcon.setAttribute('height', '15');
		clockIcon.setAttribute('viewBox', '0 0 14 15');
		clockIcon.setAttribute('fill', 'none');
		clockIcon.setAttribute('xmlns', 'http://www.w3.org/2000/svg');

		const path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
		path.setAttribute('d', 'M7 0.75C8.85652 0.75 10.637 1.4875 11.9497 2.80025C13.2625 4.11301 14 5.89348 14 7.75C14 9.60652 13.2625 11.387 11.9497 12.6997C10.637 14.0125 8.85652 14.75 7 14.75C5.14348 14.75 3.36301 14.0125 2.05025 12.6997C0.737498 11.387 0 9.60652 0 7.75C0 5.89348 0.737498 4.11301 2.05025 2.80025C3.36301 1.4875 5.14348 0.75 7 0.75ZM6.34375 4.03125V7.75C6.34375 7.96875 6.45312 8.17383 6.63633 8.29688L9.26133 10.0469C9.56211 10.2492 9.96953 10.1672 10.1719 9.86367C10.3742 9.56016 10.2922 9.15547 9.98867 8.95312L7.65625 7.4V4.03125C7.65625 3.66758 7.36367 3.375 7 3.375C6.63633 3.375 6.34375 3.66758 6.34375 4.03125Z');
		path.setAttribute('fill', '#4ADE80');

		clockIcon.appendChild(path);

		// Create animated ring container for parent
		const ringContainer = document.createElement('div');
		ringContainer.style.position = 'relative';

		// Create the center circle
		const circle = document.createElement('div');
		circle.style.width = '15px';
		circle.style.height = '15px';
		circle.style.backgroundColor = '#62bd19';
		circle.style.borderRadius = '50%';
		circle.style.top = '23px';
		circle.style.left = '23px';

		// Create the pulsing ring
		const ringring = document.createElement('div');
		ringring.style.border = '3px solid #62bd19';
		ringring.style.borderRadius = '30px';
		ringring.style.height = '25px';
		ringring.style.width = '25px';
		ringring.style.position = 'absolute';
		ringring.style.left = '-5px';
		ringring.style.top = '-5px';
		ringring.style.animation = 'pulsate 1s ease-out infinite';
		ringring.style.opacity = '0.0';

		// Add CSS animation keyframes
		const style = document.createElement('style');
		style.textContent = `
			@keyframes pulsate {
				0% { transform: scale(0.1, 0.1); opacity: 0.0; }
				50% { opacity: 1.0; }
				100% { transform: scale(1.2, 1.2); opacity: 0.0; }
			}
		`;
		document.head.appendChild(style);
		this.styleElement = style;

		// Assemble the ring container
		ringContainer.appendChild(circle);
		ringContainer.appendChild(ringring);

		// Add ring container to the parent (same level as connection button and time display)
		parentContainer?.appendChild(ringContainer);

		// Create time text
		const timeText = document.createElement('span');
		timeText.id = 'masar-time-display';

		// Update time function
		const updateTime = () => {
			const now = new Date();
			const hours = now.getHours().toString().padStart(2, '0');
			const minutes = now.getMinutes().toString().padStart(2, '0');
			const seconds = now.getSeconds().toString().padStart(2, '0');
			timeText.textContent = `${hours}:${minutes}:${seconds}`;
		};

		// Initial time update
		updateTime();

		// Update time every second for real-time display
		this.timeInterval = window.setInterval(updateTime, 1000);

		// Assemble the time display (only clock icon and time text)
		timeDisplay.appendChild(clockIcon);
		timeDisplay.appendChild(timeText);

		// Insert the time display after the connection button
		connectionButton.parentNode?.insertBefore(timeDisplay, connectionButton.nextSibling);

		// Store references
		this.timeDisplay = timeDisplay;
		this.ringContainer = ringContainer;

		logger.info('Time display added beside connection status button');
	}

	removeTimeDisplay(): void {
		const { logger } = this.options;

		if (this.timeDisplay) {
			this.timeDisplay.remove();
			this.timeDisplay = null;
		}

		if (this.ringContainer) {
			this.ringContainer.remove();
			this.ringContainer = null;
		}

		if (this.timeInterval) {
			clearInterval(this.timeInterval);
			this.timeInterval = null;
		}

		if (this.styleElement) {
			this.styleElement.remove();
			this.styleElement = null;
		}

		logger.info('Time display and ring removed');
	}
}
