export interface RaiseHandIconOptions {
	logger: any;
}

export class RaiseHandIconManager {
	private options: RaiseHandIconOptions;
	private originalIcon: Element | null = null;
	private newSvg: SVGElement | null = null;

	constructor(options: RaiseHandIconOptions) {
		this.options = options;
	}

	replaceIcon(): void {
		const { logger } = this.options;

		// Find the raise hand button
		const raiseHandButton = document.querySelector('[data-test="raiseHandBtn"]');
		if (!raiseHandButton) {
			logger.debug('Raise hand button not found');
			return;
		}

		// Find the span containing the icon
		const iconSpan = raiseHandButton.querySelector('span[color="default"]');
		if (!iconSpan) {
			logger.debug('Icon span not found');
			return;
		}

		// Find the i tag with the icon
		const originalIcon = iconSpan.querySelector('i.icon-bbb-hand');
		if (!originalIcon) {
			logger.debug('Original hand icon not found');
			return;
		}

		// Store reference to original icon
		this.originalIcon = originalIcon;

		// Create new SVG icon
		const newSvg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
		newSvg.setAttribute('width', '16');
		newSvg.setAttribute('height', '17');
		newSvg.setAttribute('viewBox', '0 0 16 17');
		newSvg.setAttribute('fill', 'none');
		newSvg.setAttribute('xmlns', 'http://www.w3.org/2000/svg');

		const path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
		path.setAttribute('d', 'M9.00012 1.5C9.00012 0.946875 8.55325 0.5 8.00012 0.5C7.447 0.5 7.00012 0.946875 7.00012 1.5V8C7.00012 8.275 6.77512 8.5 6.50012 8.5C6.22512 8.5 6.00012 8.275 6.00012 8V2.5C6.00012 1.94687 5.55324 1.5 5.00012 1.5C4.44699 1.5 4.00012 1.94687 4.00012 2.5V11C4.00012 11.0469 4.00012 11.0969 4.00324 11.1438L2.11262 9.34375C1.61262 8.86875 0.821995 8.8875 0.34387 9.3875C-0.134255 9.8875 -0.11238 10.6781 0.38762 11.1562L3.90012 14.5C5.24699 15.7844 7.03762 16.5 8.90012 16.5H9.50012C12.5376 16.5 15.0001 14.0375 15.0001 11V4.5C15.0001 3.94688 14.5532 3.5 14.0001 3.5C13.447 3.5 13.0001 3.94688 13.0001 4.5V8C13.0001 8.275 12.7751 8.5 12.5001 8.5C12.2251 8.5 12.0001 8.275 12.0001 8V2.5C12.0001 1.94687 11.5532 1.5 11.0001 1.5C10.447 1.5 10.0001 1.94687 10.0001 2.5V8C10.0001 8.275 9.77512 8.5 9.50012 8.5C9.22512 8.5 9.00012 8.275 9.00012 8V1.5Z');
		path.setAttribute('fill', '#4B5563');
		path.setAttribute('class', 'raise-hand-icon-path');

		newSvg.appendChild(path);

		// Add background to the span
		(iconSpan as HTMLElement).style.backgroundColor = '#E5E7EB';

		// Replace the i tag with the new SVG
		originalIcon.parentNode?.replaceChild(newSvg, originalIcon);

		// Store reference to new SVG
		this.newSvg = newSvg;

		logger.info('Raise hand icon replaced with custom SVG');
	}

	restoreIcon(): void {
		const { logger } = this.options;

		if (this.originalIcon && this.newSvg) {
			// Replace SVG back with original icon
			this.newSvg.parentNode?.replaceChild(this.originalIcon, this.newSvg);
			
			// Clear references
			this.originalIcon = null;
			this.newSvg = null;

			logger.info('Raise hand icon restored to original');
		}
	}
}
