/**
 * Desktop Icon Replacement Plugin
 *
 * Replaces desktop icons with custom SVG content.
 */

import type { OverlayPlugin } from '../index';
import { DesktopIconReplacement } from '../modules/desktop-icon-replacement.ts';

export const DesktopIconReplacementPlugin: OverlayPlugin = {
	id: 'desktop-icon-replacement',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('DesktopIconReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const desktopIconReplacement = new DesktopIconReplacement({ logger });
		
		// Store for cleanup
		(window as any).__masarDesktopIconReplacement = desktopIconReplacement;
		
		// Start watching for desktop icons (includes initial replacement)
		desktopIconReplacement.startWatching();
		
		logger.info('DesktopIconReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarDesktopIconReplacement) {
			const replacement = (window as any).__masarDesktopIconReplacement;
			replacement.stopWatching();
			replacement.restoreDesktopIcons();
			delete (window as any).__masarDesktopIconReplacement;
		}
		
		logger.info('DesktopIconReplacementPlugin stopped');
	},
};
