/**
 * Audio Icon Replacement Plugin
 *
 * Replaces audio icons with custom SVG content.
 */

import type { OverlayPlugin } from '../index';
import { AudioIconReplacement } from '../modules/audio-icon-replacement.ts';

export const AudioIconReplacementPlugin: OverlayPlugin = {
	id: 'audio-icon-replacement',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('AudioIconReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const audioIconReplacement = new AudioIconReplacement({ logger });
		
		// Store for cleanup
		(window as any).__masarAudioIconReplacement = audioIconReplacement;
		
		// Start watching for audio icons (includes initial replacement)
		audioIconReplacement.startWatching();
		
		logger.info('AudioIconReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarAudioIconReplacement) {
			const replacement = (window as any).__masarAudioIconReplacement;
			replacement.stopWatching();
			replacement.restoreAudioIcons();
			delete (window as any).__masarAudioIconReplacement;
		}
		
		logger.info('AudioIconReplacementPlugin stopped');
	},
};
