/**
 * Emoji Button Styling Module
 *
 * Adds yellow background styling to the emoji/reactions button.
 */

export interface EmojiButtonStylingOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class EmojiButtonStyling {
	private logger: EmojiButtonStylingOptions['logger'];
	private styledElements: Set<HTMLElement> = new Set();
	private observer: MutationObserver | null = null;
	private intervalId: number | null = null;

	constructor(options: EmojiButtonStylingOptions) {
		this.logger = options.logger;
	}

	private styleSingleEmojiButton(element: HTMLElement): void {
		// Find the span with color attribute inside the reactions button
		const span = element.querySelector('span[color]') as HTMLElement;
		if (span && !this.styledElements.has(span)) {
			// Add yellow background to the span
			span.style.setProperty('background', '#FACC15', 'important');
			this.styledElements.add(span);
			this.logger.debug('Added yellow background to emoji button span');
		}

		// Find and replace the icon with custom SVG
		const iconElement = span?.querySelector('i') as HTMLElement;
		if (iconElement && !this.styledElements.has(iconElement)) {
			// Replace the icon content with custom SVG
			iconElement.innerHTML = `<svg width="16" height="17" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M8 16.5C10.1217 16.5 12.1566 15.6571 13.6569 14.1569C15.1571 12.6566 16 10.6217 16 8.5C16 6.37827 15.1571 4.34344 13.6569 2.84315C12.1566 1.34285 10.1217 0.5 8 0.5C5.87827 0.5 3.84344 1.34285 2.34315 2.84315C0.842855 4.34344 0 6.37827 0 8.5C0 10.6217 0.842855 12.6566 2.34315 14.1569C3.84344 15.6571 5.87827 16.5 8 16.5ZM5.12813 10.6719C5.6875 11.3188 6.64375 12 8 12C9.35625 12 10.3125 11.3188 10.8719 10.6719C11.0531 10.4625 11.3687 10.4406 11.5781 10.6219C11.7875 10.8031 11.8094 11.1187 11.6281 11.3281C10.9313 12.1281 9.72188 13 8 13C6.27812 13 5.06875 12.1281 4.37187 11.3281C4.19062 11.1187 4.2125 10.8031 4.42188 10.6219C4.63125 10.4406 4.94688 10.4625 5.12813 10.6719ZM4.5125 7C4.5125 6.73478 4.61786 6.48043 4.80539 6.29289C4.99293 6.10536 5.24728 6 5.5125 6C5.77772 6 6.03207 6.10536 6.21961 6.29289C6.40714 6.48043 6.5125 6.73478 6.5125 7C6.5125 7.26522 6.40714 7.51957 6.21961 7.70711C6.03207 7.89464 5.77772 8 5.5125 8C5.24728 8 4.99293 7.89464 4.80539 7.70711C4.61786 7.51957 4.5125 7.26522 4.5125 7ZM10.5125 6C10.7777 6 11.0321 6.10536 11.2196 6.29289C11.4071 6.48043 11.5125 6.73478 11.5125 7C11.5125 7.26522 11.4071 7.51957 11.2196 7.70711C11.0321 7.89464 10.7777 8 10.5125 8C10.2473 8 9.99293 7.89464 9.80539 7.70711C9.61786 7.51957 9.5125 7.26522 9.5125 7C9.5125 6.73478 9.61786 6.48043 9.80539 6.29289C9.99293 6.10536 10.2473 6 10.5125 6Z" fill="white"/>
</svg>`;
			this.styledElements.add(iconElement);
			this.logger.debug('Replaced emoji button icon with custom SVG');
		}
	}

	styleEmojiButtons(): void {
		// Find all reactions buttons
		const reactionsButtons = document.querySelectorAll('[data-test="reactionsButton"]');
		
		reactionsButtons.forEach(button => {
			this.styleSingleEmojiButton(button as HTMLElement);
		});

		const totalStyled = reactionsButtons.length;
		if (totalStyled > 0) {
			this.logger.info(`Styled ${totalStyled} emoji buttons with yellow background`);
		} else {
			this.logger.warn('No emoji buttons found to style');
		}
	}

	startWatching(): void {
		// Initial styling
		this.styleEmojiButtons();

		// Wait for DOM to be ready if needed
		const startObserver = () => {
			// Set up MutationObserver to watch for new elements
			this.observer = new MutationObserver((mutations) => {
				let hasNewEmojiButtons = false;

				mutations.forEach((mutation) => {
					// Check added nodes
					mutation.addedNodes.forEach((node) => {
						if (node.nodeType === Node.ELEMENT_NODE) {
							const element = node as HTMLElement;
							
							// Check if the added element itself is a reactions button
							if (element.getAttribute('data-test') === 'reactionsButton') {
								this.styleSingleEmojiButton(element);
								hasNewEmojiButtons = true;
							}

							// Check if the added element contains reactions buttons
							const reactionsButtons = element.querySelectorAll?.('[data-test="reactionsButton"]');
							if (reactionsButtons) {
								reactionsButtons.forEach(button => {
									this.styleSingleEmojiButton(button as HTMLElement);
									hasNewEmojiButtons = true;
								});
							}
						}
					});
				});

				if (hasNewEmojiButtons) {
					this.logger.debug('Found and styled new emoji buttons via MutationObserver');
				}
			});

			// Start observing
			this.observer.observe(document.body, {
				childList: true,
				subtree: true
			});

			this.logger.info('Started watching for new emoji buttons');
		};

		// Start observer immediately or wait for DOM ready
		if (document.body) {
			startObserver();
		} else {
			document.addEventListener('DOMContentLoaded', startObserver);
		}

		// Also set up a periodic check as fallback (every 2 seconds)
		this.intervalId = window.setInterval(() => {
			this.logger.debug('Periodic check for emoji buttons...');
			const currentCount = document.querySelectorAll('[data-test="reactionsButton"]').length;
			if (currentCount > 0) {
				this.logger.debug(`Found ${currentCount} emoji buttons during periodic check`);
				this.styleEmojiButtons();
			}
		}, 2000);
	}

	stopWatching(): void {
		if (this.observer) {
			this.observer.disconnect();
			this.observer = null;
			this.logger.info('Stopped watching for emoji buttons');
		}
		
		if (this.intervalId) {
			clearInterval(this.intervalId);
			this.intervalId = null;
			this.logger.info('Stopped periodic emoji button checks');
		}
	}

	removeStyling(): void {
		// Remove styling from all styled elements
		this.styledElements.forEach(element => {
			element.style.removeProperty('background');
		});
		this.styledElements.clear();
		this.logger.info('Emoji button styling removed');
	}
}
