/**
 * Presentation Icon Replacement Module
 *
 * Replaces presentation icons with custom SVG content.
 */

import { PRESENTATION_OFF, PRESENTATION_ON } from '../constants/index.ts';

export interface PresentationIconReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class PresentationIconReplacement {
	private logger: PresentationIconReplacementOptions['logger'];
	private replacedElements: Map<HTMLElement, string> = new Map();
	private observer: MutationObserver | null = null;
	private intervalId: number | null = null;

	constructor(options: PresentationIconReplacementOptions) {
		this.logger = options.logger;
	}

	private replaceSinglePresentationIcon(element: HTMLElement): void {
		const hasPresentationOffClass = element.classList.contains('icon-bbb-presentation_off');
		const hasPresentationClass = element.classList.contains('icon-bbb-presentation');

		// If element was already replaced, we need to update it based on current state
		const wasAlreadyReplaced = this.replacedElements.has(element);

		if (hasPresentationOffClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with PRESENTATION_OFF SVG
			element.innerHTML = PRESENTATION_OFF;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Remove any background styling from parent span when presentation is off
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.removeProperty('background');
			}
			
			this.logger.debug('Replaced icon-bbb-presentation_off with PRESENTATION_OFF and removed background');
		} else if (hasPresentationClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with PRESENTATION_ON SVG
			element.innerHTML = PRESENTATION_ON;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Add blue background to the parent span when presentation is on
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.setProperty('background', '#1E40AF', 'important');
			}
			
			this.logger.debug('Replaced icon-bbb-presentation with PRESENTATION_ON and added blue background');
		}
	}

	replacePresentationIcons(): void {
		// Find and replace all existing presentation icons
		const presentationOffElements = document.querySelectorAll('.icon-bbb-presentation_off');
		const presentationElements = document.querySelectorAll('.icon-bbb-presentation');
		const iElementsWithPresentationOff = document.querySelectorAll('i.icon-bbb-presentation_off');
		const iElementsWithPresentation = document.querySelectorAll('i.icon-bbb-presentation');

		// Combine all elements and process them
		const allElements = [
			...Array.from(presentationOffElements),
			...Array.from(presentationElements),
			...Array.from(iElementsWithPresentationOff),
			...Array.from(iElementsWithPresentation)
		] as HTMLElement[];

		allElements.forEach(element => {
			this.replaceSinglePresentationIcon(element);
		});

		const totalReplaced = allElements.length;
		if (totalReplaced > 0) {
			this.logger.info(`Replaced ${totalReplaced} presentation icons total and removed their classes`);
		} else {
			this.logger.warn('No presentation icons found to replace');
		}
	}

	startWatching(): void {
		// Initial replacement
		this.replacePresentationIcons();

		// Wait for DOM to be ready if needed
		const startObserver = () => {
			// Set up MutationObserver to watch for new elements
			this.observer = new MutationObserver((mutations) => {
				let hasNewPresentationIcons = false;

				mutations.forEach((mutation) => {
					// Check added nodes
					mutation.addedNodes.forEach((node) => {
						if (node.nodeType === Node.ELEMENT_NODE) {
							const element = node as HTMLElement;
							
							// Check if the added element itself has presentation icon classes
							if (element.classList.contains('icon-bbb-presentation_off') || 
								element.classList.contains('icon-bbb-presentation')) {
								this.replaceSinglePresentationIcon(element);
								hasNewPresentationIcons = true;
							}

							// Check if the added element contains presentation icon elements
							const presentationOffElements = element.querySelectorAll?.('.icon-bbb-presentation_off');
							const presentationElements = element.querySelectorAll?.('.icon-bbb-presentation');
							
							if (presentationOffElements) {
								presentationOffElements.forEach(el => {
									this.replaceSinglePresentationIcon(el as HTMLElement);
									hasNewPresentationIcons = true;
								});
							}
							
							if (presentationElements) {
								presentationElements.forEach(el => {
									this.replaceSinglePresentationIcon(el as HTMLElement);
									hasNewPresentationIcons = true;
								});
							}
						}
					});

					// Check for attribute changes (class changes)
					if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
						const element = mutation.target as HTMLElement;
						// Check if this element has presentation icon classes OR if it was previously replaced
						if (element.classList.contains('icon-bbb-presentation_off') || 
							element.classList.contains('icon-bbb-presentation') ||
							this.replacedElements.has(element)) {
							this.replaceSinglePresentationIcon(element);
							hasNewPresentationIcons = true;
						}
					}
				});

				if (hasNewPresentationIcons) {
					this.logger.debug('Found and replaced new presentation icons via MutationObserver');
				}
			});

			// Start observing
			this.observer.observe(document.body, {
				childList: true,
				subtree: true,
				attributes: true,
				attributeFilter: ['class']
			});

			this.logger.info('Started watching for new presentation icons');
		};

		// Start observer immediately or wait for DOM ready
		if (document.body) {
			startObserver();
		} else {
			document.addEventListener('DOMContentLoaded', startObserver);
		}

		// Also set up a periodic check as fallback (every 2 seconds)
		this.intervalId = window.setInterval(() => {
			this.logger.debug('Periodic check for presentation icons...');
			const currentCount = document.querySelectorAll('.icon-bbb-presentation_off, .icon-bbb-presentation').length;
			if (currentCount > 0) {
				this.logger.debug(`Found ${currentCount} presentation icons during periodic check`);
				this.replacePresentationIcons();
			}
		}, 2000);
	}

	stopWatching(): void {
		if (this.observer) {
			this.observer.disconnect();
			this.observer = null;
			this.logger.info('Stopped watching for presentation icons');
		}
		
		if (this.intervalId) {
			clearInterval(this.intervalId);
			this.intervalId = null;
			this.logger.info('Stopped periodic presentation icon checks');
		}
	}

	restorePresentationIcons(): void {
		// Restore all replaced elements
		this.replacedElements.forEach((originalContent, element) => {
			element.innerHTML = originalContent;
		});
		this.replacedElements.clear();
		this.logger.info('Presentation icons restored');
	}
}
