import type { OverlayPlugin } from '../index';
import { ChatHeaderCleanerManager } from '../modules/chat-header-cleaner.ts';

export const ChatHeaderCleanerPlugin: OverlayPlugin = {
	id: 'chat-header-cleaner',
	priority: 75,
	init: async ({ logger }) => {
		logger.debug('ChatHeaderCleanerPlugin init');
	},
	start: async ({ logger }) => {
		const manager = new ChatHeaderCleanerManager({ logger });
		
		// Store manager for cleanup
		(window as any).__masarChatHeaderCleanerManager = manager;
		
		// Clean the chat header
		manager.cleanChatHeader();

		logger.info('ChatHeaderCleanerPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up manager
		if ((window as any).__masarChatHeaderCleanerManager) {
			const manager = (window as any).__masarChatHeaderCleanerManager;
			manager.restoreChatHeader();
			delete (window as any).__masarChatHeaderCleanerManager;
		}

		logger.info('ChatHeaderCleanerPlugin stopped');
	},
};
