export function whenDomReady(callback: () => void) {
	if (typeof document === 'undefined') return;
	if (document.readyState === 'complete' || document.readyState === 'interactive') {
		queueMicrotask(callback);
		return;
	}
	document.addEventListener('DOMContentLoaded', () => callback(), { once: true });
}

export function applyCssVariables(vars: Record<string, string>) {
	if (typeof document === 'undefined') return;
	const root = document.documentElement;
	for (const [key, value] of Object.entries(vars)) {
		const normalized = key.startsWith('--') ? key : `--${key}`;
		root.style.setProperty(normalized, String(value));
	}
}

export function ensureStyleTag(id: string, css?: string) {
	if (typeof document === 'undefined') return null;
	let style = document.getElementById(id) as HTMLStyleElement | null;
	if (!style) {
		style = document.createElement('style');
		style.id = id;
		document.head.appendChild(style);
	}
	if (css != null) style.textContent = css;
	return style;
}


