/**
 * App Title Enforcement Module
 * 
 * Provides robust title monitoring and enforcement to ensure the app title
 * stays consistent even when other scripts try to change it.
 */

export interface TitleEnforcerConfig {
	/** The title to enforce */
	title: string;
	/** Logging function for debug messages */
	logger?: {
		debug: (message: string) => void;
		info: (message: string) => void;
	};
}

export class TitleEnforcer {
	private config: TitleEnforcerConfig;
	private titleObserver: MutationObserver | null = null;
	private titleCheckInterval: number | null = null;
	private isActive = false;

	constructor(config: TitleEnforcerConfig) {
		this.config = config;
	}

	/**
	 * Start enforcing the title
	 */
	start(): void {
		if (this.isActive) return;

		this.isActive = true;
		
		// Set initial title
		document.title = this.config.title;
		
		// Set up monitoring
		this.setupTitleMonitoring();
		
		this.config.logger?.info(`Title enforcement started - enforcing "${this.config.title}"`);
	}

	/**
	 * Stop enforcing the title and clean up resources
	 */
	stop(): void {
		if (!this.isActive) return;

		this.isActive = false;
		
		// Clean up observers and intervals
		if (this.titleObserver) {
			this.titleObserver.disconnect();
			this.titleObserver = null;
		}
		
		if (this.titleCheckInterval) {
			clearInterval(this.titleCheckInterval);
			this.titleCheckInterval = null;
		}
		
		this.config.logger?.info('Title enforcement stopped');
	}

	/**
	 * Force the title to be set to the configured value
	 */
	enforce(): void {
		if (document.title !== this.config.title) {
			document.title = this.config.title;
			this.config.logger?.debug(`Title restored to "${this.config.title}"`);
		}
	}

	/**
	 * Set up the title monitoring system
	 */
	private setupTitleMonitoring(): void {
		// Monitor document title changes via MutationObserver
		this.titleObserver = new MutationObserver((mutations) => {
			mutations.forEach((mutation) => {
				if (mutation.type === 'childList' && mutation.target === document.head) {
					// Check if title tag was modified
					const titleTag = document.querySelector('title');
					if (titleTag && titleTag.textContent !== this.config.title) {
						this.enforce();
					}
				}
			});
		});
		
		// Observe head for title changes
		this.titleObserver.observe(document.head, {
			childList: true,
			subtree: true,
			characterData: true
		});
		
		// Also use periodic check as backup (every 1000ms)
		this.titleCheckInterval = window.setInterval(() => {
			this.enforce();
		}, 1000);
	}
}

/**
 * Convenience function to create and start a title enforcer
 */
export function createTitleEnforcer(config: TitleEnforcerConfig): TitleEnforcer {
	const enforcer = new TitleEnforcer(config);
	enforcer.start();
	return enforcer;
}
