/**
 * Desktop Icon Replacement Module
 *
 * Replaces desktop icons with custom SVG content.
 */

import { DESKTOP_OFF, DESKTOP_ON } from '../constants/index.ts';

export interface DesktopIconReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class DesktopIconReplacement {
	private logger: DesktopIconReplacementOptions['logger'];
	private replacedElements: Map<HTMLElement, string> = new Map();
	private observer: MutationObserver | null = null;
	private intervalId: number | null = null;

	constructor(options: DesktopIconReplacementOptions) {
		this.logger = options.logger;
	}

	private replaceSingleDesktopIcon(element: HTMLElement): void {
		const hasDesktopOffClass = element.classList.contains('icon-bbb-desktop_off');
		const hasDesktopClass = element.classList.contains('icon-bbb-desktop');

		// If element was already replaced, we need to update it based on current state
		const wasAlreadyReplaced = this.replacedElements.has(element);

		if (hasDesktopOffClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with DESKTOP_OFF SVG
			element.innerHTML = DESKTOP_OFF;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Remove any background styling from parent span when desktop sharing is off
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.removeProperty('background');
			}
			
			this.logger.debug('Replaced icon-bbb-desktop_off with DESKTOP_OFF and removed background');
		} else if (hasDesktopClass) {
			// Store original content for restoration (only if not already stored)
			if (!wasAlreadyReplaced) {
				this.replacedElements.set(element, element.innerHTML);
			}
			// Replace with DESKTOP_ON SVG
			element.innerHTML = DESKTOP_ON;
			// Remove the class completely and hide any background icons
			element.className = '';
			element.style.display = 'inline-block';
			element.style.backgroundImage = 'none';
			element.style.background = 'none';
			
			// Add blue background to the parent span when desktop sharing is on
			const parentSpan = element.closest('span[color]') as HTMLElement;
			if (parentSpan) {
				parentSpan.style.setProperty('background', '#1E40AF', 'important');
			}
			
			this.logger.debug('Replaced icon-bbb-desktop with DESKTOP_ON and added blue background');
		}
	}

	replaceDesktopIcons(): void {
		// Find and replace all existing desktop icons
		const desktopOffElements = document.querySelectorAll('.icon-bbb-desktop_off');
		const desktopElements = document.querySelectorAll('.icon-bbb-desktop');
		const iElementsWithDesktopOff = document.querySelectorAll('i.icon-bbb-desktop_off');
		const iElementsWithDesktop = document.querySelectorAll('i.icon-bbb-desktop');

		// Combine all elements and process them
		const allElements = [
			...Array.from(desktopOffElements),
			...Array.from(desktopElements),
			...Array.from(iElementsWithDesktopOff),
			...Array.from(iElementsWithDesktop)
		] as HTMLElement[];

		allElements.forEach(element => {
			this.replaceSingleDesktopIcon(element);
		});

		const totalReplaced = allElements.length;
		if (totalReplaced > 0) {
			this.logger.info(`Replaced ${totalReplaced} desktop icons total and removed their classes`);
		} else {
			this.logger.warn('No desktop icons found to replace');
		}
	}

	startWatching(): void {
		// Initial replacement
		this.replaceDesktopIcons();

		// Wait for DOM to be ready if needed
		const startObserver = () => {
			// Set up MutationObserver to watch for new elements
			this.observer = new MutationObserver((mutations) => {
				let hasNewDesktopIcons = false;

				mutations.forEach((mutation) => {
					// Check added nodes
					mutation.addedNodes.forEach((node) => {
						if (node.nodeType === Node.ELEMENT_NODE) {
							const element = node as HTMLElement;
							
							// Check if the added element itself has desktop icon classes
							if (element.classList.contains('icon-bbb-desktop_off') || 
								element.classList.contains('icon-bbb-desktop')) {
								this.replaceSingleDesktopIcon(element);
								hasNewDesktopIcons = true;
							}

							// Check if the added element contains desktop icon elements
							const desktopOffElements = element.querySelectorAll?.('.icon-bbb-desktop_off');
							const desktopElements = element.querySelectorAll?.('.icon-bbb-desktop');
							
							if (desktopOffElements) {
								desktopOffElements.forEach(el => {
									this.replaceSingleDesktopIcon(el as HTMLElement);
									hasNewDesktopIcons = true;
								});
							}
							
							if (desktopElements) {
								desktopElements.forEach(el => {
									this.replaceSingleDesktopIcon(el as HTMLElement);
									hasNewDesktopIcons = true;
								});
							}
						}
					});

					// Check for attribute changes (class changes)
					if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
						const element = mutation.target as HTMLElement;
						// Check if this element has desktop icon classes OR if it was previously replaced
						if (element.classList.contains('icon-bbb-desktop_off') || 
							element.classList.contains('icon-bbb-desktop') ||
							this.replacedElements.has(element)) {
							this.replaceSingleDesktopIcon(element);
							hasNewDesktopIcons = true;
						}
					}
				});

				if (hasNewDesktopIcons) {
					this.logger.debug('Found and replaced new desktop icons via MutationObserver');
				}
			});

			// Start observing
			this.observer.observe(document.body, {
				childList: true,
				subtree: true,
				attributes: true,
				attributeFilter: ['class']
			});

			this.logger.info('Started watching for new desktop icons');
		};

		// Start observer immediately or wait for DOM ready
		if (document.body) {
			startObserver();
		} else {
			document.addEventListener('DOMContentLoaded', startObserver);
		}

		// Also set up a periodic check as fallback (every 2 seconds)
		this.intervalId = window.setInterval(() => {
			this.logger.debug('Periodic check for desktop icons...');
			const currentCount = document.querySelectorAll('.icon-bbb-desktop_off, .icon-bbb-desktop').length;
			if (currentCount > 0) {
				this.logger.debug(`Found ${currentCount} desktop icons during periodic check`);
				this.replaceDesktopIcons();
			}
		}, 2000);
	}

	stopWatching(): void {
		if (this.observer) {
			this.observer.disconnect();
			this.observer = null;
			this.logger.info('Stopped watching for desktop icons');
		}
		
		if (this.intervalId) {
			clearInterval(this.intervalId);
			this.intervalId = null;
			this.logger.info('Stopped periodic desktop icon checks');
		}
	}

	restoreDesktopIcons(): void {
		// Restore all replaced elements
		this.replacedElements.forEach((originalContent, element) => {
			element.innerHTML = originalContent;
		});
		this.replacedElements.clear();
		this.logger.info('Desktop icons restored');
	}
}
