import type { OverlayPlugin } from '../index';
import { RaiseHandIconManager } from '../modules/raise-hand-icon.ts';

export const RaiseHandIconPlugin: OverlayPlugin = {
	id: 'raise-hand-icon',
	priority: 80,
	init: async ({ logger }) => {
		logger.debug('RaiseHandIconPlugin init');
	},
	start: async ({ logger }) => {
		// Initialize raise hand icon manager
		const raiseHandIconManager = new RaiseHandIconManager({
			logger: logger
		});

		// Store manager for cleanup
		(window as any).__masarRaiseHandIconManager = raiseHandIconManager;

		// Replace the raise hand icon
		raiseHandIconManager.replaceIcon();

		logger.info('RaiseHandIconPlugin started');
	},
	stop: async ({ logger }) => {
		// Restore original icon
		if ((window as any).__masarRaiseHandIconManager) {
			(window as any).__masarRaiseHandIconManager.restoreIcon();
			delete (window as any).__masarRaiseHandIconManager;
		}
		logger.info('RaiseHandIconPlugin stopped');
	},
};
