/**
 * Navbar Toggle Replacement Plugin
 *
 * Replaces the navbar toggle button content with the Masar logo
 * while preserving the original button structure and functionality.
 */

import type { OverlayPlugin } from '../index';
import { NavbarToggleReplacement } from '../modules/navbar-toggle-replacement.ts';

export const NavbarToggleReplacementPlugin: OverlayPlugin = {
	id: 'navbar-toggle-replacement',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('NavbarToggleReplacementPlugin init');
	},
	start: async ({ logger }) => {
		const navbarToggleReplacement = new NavbarToggleReplacement({ logger });
		
		// Store for cleanup
		(window as any).__masarNavbarToggleReplacement = navbarToggleReplacement;
		
		// Apply the replacement
		navbarToggleReplacement.replaceToggleContent();
		
		logger.info('NavbarToggleReplacementPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarNavbarToggleReplacement) {
			(window as any).__masarNavbarToggleReplacement.restoreToggleContent();
			delete (window as any).__masarNavbarToggleReplacement;
		}
		
		logger.info('NavbarToggleReplacementPlugin stopped');
	},
};
