/**
 * Theme Management Module
 * 
 * Handles theme application and management via data attributes.
 */

export interface ThemeConfig {
	/** The theme name to apply */
	themeName: string;
	/** Logging function for debug messages */
	logger?: {
		debug: (message: string) => void;
		info: (message: string) => void;
	};
}

export class ThemeManager {
	private config: ThemeConfig;
	private originalTheme: string | null = null;

	constructor(config: ThemeConfig) {
		this.config = config;
	}

	/**
	 * Apply the configured theme
	 */
	apply(): void {
		if (typeof document === 'undefined') return;

		// Store original theme for restoration
		this.originalTheme = document.documentElement.getAttribute('data-theme');
		
		// Apply new theme
		document.documentElement.setAttribute('data-theme', this.config.themeName);
		
		this.config.logger?.info(`Theme "${this.config.themeName}" applied`);
	}

	/**
	 * Restore the original theme
	 */
	restore(): void {
		if (typeof document === 'undefined') return;

		if (this.originalTheme) {
			document.documentElement.setAttribute('data-theme', this.originalTheme);
		} else {
			document.documentElement.removeAttribute('data-theme');
		}

		this.config.logger?.info('Theme restored');
	}

	/**
	 * Remove theme attribute completely
	 */
	remove(): void {
		if (typeof document === 'undefined') return;

		document.documentElement.removeAttribute('data-theme');
		this.config.logger?.info('Theme removed');
	}
}
