/**
 * Logo Injection Module
 * 
 * Handles logo placement and injection into various page elements.
 */

export interface LogoConfig {
	/** The logo URL (can be data URL) */
	logoUrl: string;
	/** Alt text for the logo */
	altText: string;
	/** CSS class name for the logo */
	className: string;
	/** Logging function for debug messages */
	logger?: {
		debug: (message: string) => void;
		info: (message: string) => void;
	};
}

export class LogoInjector {
	private config: LogoConfig;
	private injectedLogos: HTMLElement[] = [];

	constructor(config: LogoConfig) {
		this.config = config;
	}

	/**
	 * Inject logo into page elements
	 */
	inject(): void {
		if (typeof document === 'undefined') return;

		// Selectors to look for title elements
		const titleSelectors = [
			'h1', 'h2', '.title', '.header-title', 
			'[data-testid*="title"]', '.navbar-brand',
			'.meeting-title', '.room-title',
			// BBB specific selectors
			'.navbar', '.header', '.top-bar', '.main-header',
			'[class*="title"]', '[class*="header"]', '[class*="navbar"]',
			'.bbb-header', '.meeting-header', '.presentation-title'
		];

		let logoAdded = false;
		for (const selector of titleSelectors) {
			const titleEl = document.querySelector(selector);
			if (titleEl && !titleEl.querySelector(`.${this.config.className}`)) {
				const logo = this.createLogoElement();
				titleEl.insertBefore(logo, titleEl.firstChild);
				this.injectedLogos.push(logo);
				logoAdded = true;
				this.config.logger?.info(`Logo added to ${selector}`);
				break;
			}
		}

		// Fallback: create a custom title bar if no existing title found
		if (!logoAdded) {
			this.createCustomTitleBar();
		}
	}

	/**
	 * Remove all injected logos
	 */
	remove(): void {
		this.injectedLogos.forEach(logo => logo.remove());
		this.injectedLogos = [];
		
		// Also remove any logos with the class name (fallback cleanup)
		document.querySelectorAll(`.${this.config.className}`).forEach(logo => logo.remove());
		
		this.config.logger?.info('All logos removed');
	}

	/**
	 * Create a logo element
	 */
	private createLogoElement(): HTMLElement {
		const logo = document.createElement('img');
		logo.src = this.config.logoUrl;
		logo.alt = this.config.altText;
		logo.className = this.config.className;
		logo.style.cssText = `
			width: 24px;
			height: 24px;
			margin-right: 8px;
			vertical-align: middle;
			display: inline-block;
		`;
		return logo;
	}

	/**
	 * Create a custom title bar as fallback
	 */
	private createCustomTitleBar(): void {
		const customTitle = document.createElement('div');
		customTitle.className = 'masar-custom-title session-custom-title';

		const logo = document.createElement('img');
		logo.src = this.config.logoUrl;
		logo.alt = this.config.altText;
		logo.className = this.config.className;
		logo.style.cssText = `
			width: 20px;
			height: 20px;
			margin-right: 6px;
		`;

		const text = document.createElement('span');
		text.textContent = 'مسار التعلمية - Live';

		customTitle.appendChild(logo);
		customTitle.appendChild(text);
		document.body.appendChild(customTitle);
		this.injectedLogos.push(customTitle);

		this.config.logger?.info('Custom title bar created');
	}
}
