import type { OverlayPlugin } from '../index';
import '../styles/session-styles.css';
import { TitleEnforcer } from '../modules/app-title-enforce.ts';
import { FaviconManager } from '../modules/favicon-manager.ts';
import { LogoInjector } from '../modules/logo-injector.ts';
import { ThemeManager } from '../modules/theme-manager.ts';
import { MASAR_LOGO_SVG, APP_TITLE, THEME_NAME, LOGO_CLASS_NAME, LOGO_ALT_TEXT } from '../constants/index.ts';

export const SessionPlugin: OverlayPlugin = {
	id: 'session-plugin',
	priority: -100,
	init: async ({ logger }) => {
		logger.debug('SessionPlugin init');
	},
	start: async ({ logger }) => {
		// Initialize managers
		const themeManager = new ThemeManager({
			themeName: THEME_NAME,
			logger: logger
		});
		
		const titleEnforcer = new TitleEnforcer({
			title: APP_TITLE,
			logger: logger
		});
		
		const faviconManager = new FaviconManager({
			faviconUrl: MASAR_LOGO_SVG,
			logger: logger
		});
		
		const logoInjector = new LogoInjector({
			logoUrl: MASAR_LOGO_SVG,
			altText: LOGO_ALT_TEXT,
			className: LOGO_CLASS_NAME,
			logger: logger
		});
		
		// Store managers for cleanup
		(window as any).__masarManagers = {
			themeManager,
			titleEnforcer,
			faviconManager,
			logoInjector
		};
		
		// Apply all configurations
		themeManager.apply();
		titleEnforcer.start();
		faviconManager.apply();
		logoInjector.inject();
		
		logger.info('SessionPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up all managers
		if ((window as any).__masarManagers) {
			const { themeManager, titleEnforcer, faviconManager, logoInjector } = (window as any).__masarManagers;
			
			themeManager.remove();
			titleEnforcer.stop();
			faviconManager.restore();
			logoInjector.remove();
			
			delete (window as any).__masarManagers;
		}
		
		logger.info('SessionPlugin stopped');
	},
};