export interface ChatHeaderCleanerOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class ChatHeaderCleanerManager {
	private originalButton: HTMLElement | null = null;
	private originalIcon: HTMLElement | null = null;
	private leftArrowElements: HTMLElement[] = [];
	private logger: ChatHeaderCleanerOptions['logger'];

	constructor(options: ChatHeaderCleanerOptions) {
		this.logger = options.logger;
	}

	cleanChatHeader(): void {
		// Find the hide public chat button
		const button = document.querySelector('[data-test="hidePublicChat"]') as HTMLElement;
		if (!button) {
			this.logger.warn('Hide public chat button not found');
			return;
		}

		this.logger.debug('Found hide public chat button, cleaning header');

		// Store reference to original button
		this.originalButton = button;

		// Find the icon element inside the button
		const iconElement = button.querySelector('i') as HTMLElement;
		if (iconElement) {
			this.logger.debug('Found icon element, removing it');
			this.originalIcon = iconElement;
			
			// Remove the icon element
			iconElement.remove();
		} else {
			this.logger.debug('No icon element found in button');
		}

		// Find and remove all elements with icon-bbb-left_arrow class
		const leftArrowElements = document.querySelectorAll('.icon-bbb-left_arrow') as NodeListOf<HTMLElement>;
		if (leftArrowElements.length > 0) {
			this.logger.debug(`Found ${leftArrowElements.length} left arrow elements, removing them`);
			
			leftArrowElements.forEach((element, index) => {
				// Store reference for potential restoration
				this.leftArrowElements.push(element);
				
				// Remove the element
				element.remove();
				this.logger.debug(`Removed left arrow element ${index + 1}`);
			});
		} else {
			this.logger.debug('No left arrow elements found');
		}

		this.logger.info('Chat header cleaned - icon removed, left arrows removed, text preserved');
	}

	restoreChatHeader(): void {
		if (this.originalButton && this.originalIcon) {
			// Find the span element to insert the icon before it
			const spanElement = this.originalButton.querySelector('span');
			if (spanElement) {
				// Insert the icon before the span
				this.originalButton.insertBefore(this.originalIcon, spanElement);
			} else {
				// If no span found, just append to the button
				this.originalButton.appendChild(this.originalIcon);
			}
		}

		// Note: Left arrow elements cannot be easily restored as we don't know their original positions
		// This is a limitation of removing elements from the DOM
		if (this.leftArrowElements.length > 0) {
			this.logger.warn('Left arrow elements cannot be restored - they were removed from DOM');
		}

		this.logger.info('Chat header restored (icon only - left arrows cannot be restored)');
	}
}
