export interface NavbarRepositionerOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class NavbarRepositionerManager {
	private secondChild: HTMLElement | null = null;
	private originalParent: HTMLElement | null = null;
	private originalNextSibling: Node | null = null;
	private logger: NavbarRepositionerOptions['logger'];

	constructor(options: NavbarRepositionerOptions) {
		this.logger = options.logger;
	}

	repositionNavbarChild(): void {
		// Find the navbar element
		const navbar = document.querySelector('#Navbar') as HTMLElement;
		if (!navbar) {
			this.logger.warn('Navbar element not found');
			return;
		}

		this.logger.debug('Found navbar element');

		// Get the second child of the navbar
		const children = navbar.children;
		if (children.length < 2) {
			this.logger.warn('Navbar does not have a second child');
			return;
		}

		const secondChild = children[1] as HTMLElement;
		this.logger.debug('Found navbar second child');

		// Find the presentation container
		const presentationContainer = document.querySelector('[data-test="presentationContainer"]') as HTMLElement;
		if (!presentationContainer) {
			this.logger.warn('Presentation container not found');
			return;
		}

		this.logger.debug('Found presentation container');

		// Store references for potential restoration
		this.secondChild = secondChild;
		this.originalParent = secondChild.parentElement;
		this.originalNextSibling = secondChild.nextSibling;

		// Move the second child to be the first child of the presentation container
		if (presentationContainer.firstChild) {
			// Insert before the first child
			presentationContainer.insertBefore(secondChild, presentationContainer.firstChild);
		} else {
			// If no children, just append
			presentationContainer.appendChild(secondChild);
		}

		// Apply padding to the moved element
		secondChild.style.padding = '4px 15px 5px 15px';

		this.logger.info('Navbar second child moved to presentation container as first child with padding applied');
	}

	restoreNavbarChild(): void {
		if (this.secondChild && this.originalParent) {
			// Remove the padding that was applied
			this.secondChild.style.padding = '';

			// Move the child back to its original position
			if (this.originalNextSibling) {
				// Insert before the original next sibling
				this.originalParent.insertBefore(this.secondChild, this.originalNextSibling);
			} else {
				// If no next sibling, append to the end
				this.originalParent.appendChild(this.secondChild);
			}

			this.logger.info('Navbar child restored to original position with padding removed');
		} else {
			this.logger.warn('Cannot restore navbar child - missing references');
		}
	}
}
