import { HEADER_LOGO_SVG } from '../constants/index.ts';

export interface HeaderLogoReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class HeaderLogoReplacementManager {
	private originalButton: HTMLElement | null = null;
	private customLogo: HTMLElement | null = null;
	private logger: HeaderLogoReplacementOptions['logger'];

	constructor(options: HeaderLogoReplacementOptions) {
		this.logger = options.logger;
	}

	replaceHeaderButton(): void {
		// Find the user list toggle button
		const button = document.querySelector('[data-test="toggleUserList"]') as HTMLElement;
		if (!button) {
			this.logger.warn('User list toggle button not found');
			return;
		}

		this.logger.debug('Found user list toggle button, replacing with Masar logo');

		// Store reference to original button
		this.originalButton = button;

		// Hide the original button off-screen but keep it functional
		button.style.position = 'absolute';
		button.style.left = '-9999px';
		button.style.opacity = '0';
		button.style.pointerEvents = 'none';

		// Create custom logo container
		const logoContainer = document.createElement('div');
		logoContainer.className = 'masar-header-logo-replacement';
		logoContainer.style.cursor = 'pointer';
		logoContainer.style.display = 'flex';
		logoContainer.style.alignItems = 'center';
		logoContainer.style.justifyContent = 'center';
		logoContainer.style.height = '32px';
		logoContainer.style.width = 'auto';

		// Create the logo element
		const logoElement = document.createElement('div');
		logoElement.innerHTML = HEADER_LOGO_SVG;
		logoElement.style.height = '32px';
		logoElement.style.width = 'auto';

		// Scale the SVG to fit the header
		const svgElement = logoElement.querySelector('svg');
		if (svgElement) {
			svgElement.style.height = '32px';
			svgElement.style.width = 'auto';
			svgElement.style.maxWidth = '80px'; // Reasonable max width for header
		}

		logoContainer.appendChild(logoElement);

		// Add click handler to forward clicks to original button
		logoContainer.addEventListener('click', (event) => {
			event.preventDefault();
			event.stopPropagation();
			
			if (this.originalButton) {
				this.logger.debug('Forwarding click to original toggle button');
				this.originalButton.click();
			}
		});

		// Insert the custom logo after the original button
		button.parentNode?.insertBefore(logoContainer, button.nextSibling);
		
		this.customLogo = logoContainer;
		this.logger.info('Header logo replacement completed');
	}

	restoreHeaderButton(): void {
		if (this.originalButton) {
			// Restore original button visibility and functionality
			this.originalButton.style.position = '';
			this.originalButton.style.left = '';
			this.originalButton.style.opacity = '';
			this.originalButton.style.pointerEvents = '';
		}

		if (this.customLogo) {
			// Remove custom logo
			this.customLogo.remove();
			this.customLogo = null;
		}

		this.logger.info('Header button restored');
	}
}
