import './styles/theme.css';
import { PluginRegistry } from './internal/registry.ts';
import { applyCssVariables, whenDomReady } from './internal/dom.ts';
import { SessionPlugin } from './plugins/session-plugin.ts';
import { TimeDisplayPlugin } from './plugins/time-display.ts';
import { RaiseHandIconPlugin } from './plugins/raise-hand-icon.ts';
import { NotificationBannerRemovalPlugin } from './plugins/notification-banner-removal.ts';
import { HeaderLogoReplacementPlugin } from './plugins/header-logo-replacement.ts';
import { ChatHeaderCleanerPlugin } from './plugins/chat-header-cleaner.ts';
import { NavbarRepositionerPlugin } from './plugins/navbar-repositioner.ts';
import { NavbarToggleReplacementPlugin } from './plugins/navbar-toggle-replacement.ts';
import { DesktopIconReplacementPlugin } from './plugins/desktop-icon-replacement.ts';
import { VideoIconReplacementPlugin } from './plugins/video-icon-replacement.ts';
import { AudioIconReplacementPlugin } from './plugins/audio-icon-replacement.ts';
import { EmojiButtonStylingPlugin } from './plugins/emoji-button-styling';
import { PresentationIconReplacementPlugin } from './plugins/presentation-icon-replacement';

// Expose package version via Vite define
declare const __PACKAGE_VERSION__: string;

export type OverlayLogLevel = 'silent' | 'error' | 'warn' | 'info' | 'debug';

export interface OverlayConfig {
  logLevel: OverlayLogLevel;
  themeVariables: Record<string, string>;
  autoStart: boolean;
}

export interface OverlayContext {
  config: OverlayConfig;
  logger: {
    debug: (...args: unknown[]) => void;
    info: (...args: unknown[]) => void;
    warn: (...args: unknown[]) => void;
    error: (...args: unknown[]) => void;
  };
}

export interface OverlayPlugin {
  id: string;
  priority?: number;
  init?: (ctx: OverlayContext) => void | Promise<void>;
  start?: (ctx: OverlayContext) => void | Promise<void>;
  stop?: (ctx: OverlayContext) => void | Promise<void>;
}

const DEFAULT_CONFIG: OverlayConfig = {
  logLevel: 'info',
  themeVariables: {},
  autoStart: true,
};

function makeLogger(level: OverlayLogLevel) {
  const order: Record<OverlayLogLevel, number> = {
    silent: 0,
    error: 1,
    warn: 2,
    info: 3,
    debug: 4,
  };
  const current = order[level] ?? 3;
  return {
    debug: (...args: unknown[]) => {
      if (current >= 4) console.debug('[MasarBBBOverlay]', ...args);
    },
    info: (...args: unknown[]) => {
      if (current >= 3) console.info('[MasarBBBOverlay]', ...args);
    },
    warn: (...args: unknown[]) => {
      if (current >= 2) console.warn('[MasarBBBOverlay]', ...args);
    },
    error: (...args: unknown[]) => {
      if (current >= 1) console.error('[MasarBBBOverlay]', ...args);
    },
  };
}

const registry = new PluginRegistry();
// Register built-in plugins; users can disable or override by not calling them
registry.register(SessionPlugin);
registry.register(TimeDisplayPlugin);
registry.register(RaiseHandIconPlugin);
registry.register(NotificationBannerRemovalPlugin);
registry.register(HeaderLogoReplacementPlugin);
registry.register(ChatHeaderCleanerPlugin);
  registry.register(NavbarRepositionerPlugin);
  registry.register(NavbarToggleReplacementPlugin);
  registry.register(DesktopIconReplacementPlugin);
  registry.register(VideoIconReplacementPlugin);
  registry.register(AudioIconReplacementPlugin);
  registry.register(EmojiButtonStylingPlugin);
  registry.register(PresentationIconReplacementPlugin);
let started = false;
let context: OverlayContext | null = null;

async function boot(config?: Partial<OverlayConfig>) {
  if (started) return;
  const merged: OverlayConfig = { ...DEFAULT_CONFIG, ...(config || {}) };
  const logger = makeLogger(merged.logLevel);
  context = { config: merged, logger };

  logger.info(`v${__PACKAGE_VERSION__} initializing`);
  // Requested custom log
  console.log('Hello from masar sdk');

  // Apply theme variables early
  if (merged.themeVariables && Object.keys(merged.themeVariables).length > 0) {
    applyCssVariables(merged.themeVariables);
  }

  // Initialize all registered plugins
  await registry.initAll(context);

  if (merged.autoStart) {
    await registry.startAll(context);
    started = true;
    logger.info('started');
  }
}

async function start() {
  if (!context) {
    await boot();
    return;
  }
  if (started) return;
  await registry.startAll(context);
  started = true;
}

async function stop() {
  if (!context || !started) return;
  await registry.stopAll(context);
  started = false;
}

function registerPlugin(plugin: OverlayPlugin) {
  registry.register(plugin);
}

function setThemeVariables(vars: Record<string, string>) {
  applyCssVariables(vars);
  if (context) context.config.themeVariables = { ...context.config.themeVariables, ...vars };
}

function getVersion() {
  return __PACKAGE_VERSION__;
}

export const MasarBBBOverlay = {
  init: boot,
  start,
  stop,
  registerPlugin,
  setThemeVariables,
  getVersion,
};

declare global {
  interface Window {
    MASAR_BBB_OVERLAY?: typeof MasarBBBOverlay;
  }
}

// Auto-attach to window for IIFE usage
if (typeof window !== 'undefined') {
  if (!window.MASAR_BBB_OVERLAY) {
    window.MASAR_BBB_OVERLAY = MasarBBBOverlay;
  }
  // Initialize on DOM ready if a global config is present
  whenDomReady(() => {
    const maybeConfig = (window as unknown as { MASAR_BBB_OVERLAY_CONFIG?: Partial<OverlayConfig> })
      .MASAR_BBB_OVERLAY_CONFIG;
    if (maybeConfig) {
      void boot(maybeConfig);
    }
  });
}

export default MasarBBBOverlay;


