/**
 * Emoji Button Styling Plugin
 *
 * Plugin that applies yellow background styling to emoji/reactions buttons.
 */

import type { OverlayPlugin } from '../index';
import { EmojiButtonStyling } from '../modules/emoji-button-styling.ts';

export const EmojiButtonStylingPlugin: OverlayPlugin = {
	id: 'emoji-button-styling',
	priority: 10,
	init: async ({ logger }) => {
		logger.debug('EmojiButtonStylingPlugin init');
	},
	start: async ({ logger }) => {
		const emojiButtonStyling = new EmojiButtonStyling({ logger });
		
		// Store for cleanup
		(window as any).__masarEmojiButtonStyling = emojiButtonStyling;
		
		// Start watching for emoji buttons (includes initial styling)
		emojiButtonStyling.startWatching();
		
		logger.info('EmojiButtonStylingPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up
		if ((window as any).__masarEmojiButtonStyling) {
			const styling = (window as any).__masarEmojiButtonStyling;
			styling.stopWatching();
			styling.removeStyling();
			delete (window as any).__masarEmojiButtonStyling;
		}
		
		logger.info('EmojiButtonStylingPlugin stopped');
	},
};
