import type { OverlayPlugin } from '../index';
import { NavbarRepositionerManager } from '../modules/navbar-repositioner.ts';

export const NavbarRepositionerPlugin: OverlayPlugin = {
	id: 'navbar-repositioner',
	priority: 70,
	init: async ({ logger }) => {
		logger.debug('NavbarRepositionerPlugin init');
	},
	start: async ({ logger }) => {
		const manager = new NavbarRepositionerManager({ logger });
		
		// Store manager for cleanup
		(window as any).__masarNavbarRepositionerManager = manager;
		
		// Reposition the navbar child
		manager.repositionNavbarChild();

		logger.info('NavbarRepositionerPlugin started');
	},
	stop: async ({ logger }) => {
		// Clean up manager
		if ((window as any).__masarNavbarRepositionerManager) {
			const manager = (window as any).__masarNavbarRepositionerManager;
			manager.restoreNavbarChild();
			delete (window as any).__masarNavbarRepositionerManager;
		}

		logger.info('NavbarRepositionerPlugin stopped');
	},
};
