/**
 * Navbar Toggle Replacement Module
 *
 * Handles replacing the navbar toggle button content with the Masar logo
 * while preserving the original button structure and functionality.
 */

import { HEADER_LOGO } from '../constants/index.ts';

export interface NavbarToggleReplacementOptions {
	logger: {
		debug: (...args: any[]) => void;
		info: (...args: any[]) => void;
		warn: (...args: any[]) => void;
		error: (...args: any[]) => void;
	};
}

export class NavbarToggleReplacement {
	private logger: NavbarToggleReplacementOptions['logger'];
	private originalElement: HTMLElement | null = null;

	constructor(options: NavbarToggleReplacementOptions) {
		this.logger = options.logger;
	}

	replaceToggleContent(): void {
		// Find the button with data-test="hasUnreadMessages"
		const toggleButton = document.querySelector('button[data-test="hasUnreadMessages"]') as HTMLElement;
		if (!toggleButton) {
			this.logger.warn('Navbar toggle button not found');
			return;
		}

		this.logger.debug('Found navbar toggle button, hiding content and adding Masar logo');

		// Store reference to original element
		this.originalElement = toggleButton;

		// Hide the original content by setting opacity to 0
		toggleButton.style.opacity = '0';

		// Create a new logo element
		const logoElement = document.createElement('div');
		logoElement.className = 'masar-navbar-toggle-logo';
		logoElement.innerHTML = HEADER_LOGO;

		// Style the logo element
		logoElement.style.position = 'absolute';
		logoElement.style.top = '0';
		logoElement.style.left = '0';
		logoElement.style.width = '100%';
		logoElement.style.height = '100%';
		logoElement.style.display = 'flex';
		logoElement.style.alignItems = 'center';
		logoElement.style.justifyContent = 'center';

		// Style the SVG to fit properly
		const svgElement = logoElement.querySelector('svg');
		if (svgElement) {
			svgElement.style.height = '24px';
			svgElement.style.width = 'auto';
			svgElement.style.maxWidth = '60px';
			svgElement.style.display = 'block';
		}

		// Make the parent element position relative to contain the absolute logo
		toggleButton.style.position = 'relative';

		// Add the logo element to the toggle button
		toggleButton.appendChild(logoElement);

		this.logger.info('Navbar toggle button content hidden and Masar logo added');
	}

	restoreToggleContent(): void {
		if (this.originalElement) {
			// Restore original opacity
			this.originalElement.style.opacity = '';
			
			// Remove the logo element
			const logoElement = this.originalElement.querySelector('.masar-navbar-toggle-logo');
			if (logoElement) {
				logoElement.remove();
			}
			
			// Restore original position
			this.originalElement.style.position = '';
		}

		this.logger.info('Navbar toggle button content restored');
	}
}
